.link {
    @apply underline decoration-transparent transition-colors duration-300 ease-in-out;

    &:hover {
        @apply duration-100 decoration-inherit cursor-pointer;
    }

    @media (hover: none) {
        @apply decoration-inherit;
    }
}

.responsiveContainer {
    @apply sm:px-12 md:px-32 lg:px-48;
}

.App {
    a {
        @apply link;
    }
}

.codeEditor {
    pre {
        @apply shadow-2xl m-[2px] rounded focus:ring-2 transition-shadow focus:ring-neutral-500 focus:ring-opacity-50 font-mono #{!important};
    }
}
